.profile__title {
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  grid-column: 1;
  grid-row: 1;
  max-width: 294px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 528px) {
  .profile__title {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    max-width: 189px;
    margin: 0 auto;
  }
}
